import {
  AccountType,
  Country,
  ExternalIdentifierType,
  IndustrySegment,
  IndustryVertical,
  MarketSize,
  PartyType,
  PhoneFaxRole,
  PhoneFaxType,
  StateProvince,
  Status,
} from "types";
import axios from "axios";
import { sortByText } from "utils";
import {AuthHeader} from "./dataService";

const baseUrl = "http://104.154.103.21:8081/api/v1";

export const listCountries = async () => {
  try {
    const response = await axios.get<{ "country:": Country[] }>(
      `${baseUrl}/country`, { headers: AuthHeader() });
    return sortByText(response.data["country:"], "countryName");
  } catch (error) {
    throw error;
  }
};

