import React, { useState } from "react";
import { Button } from "../../components/Button/Button";
import { Panel } from "../../components/Panel/Panel";
import { TextField } from "../../components/TextField/TextField";
import s from "./loginpage.module.scss";
import { Loader } from "components/Loader/Loader";
import { useDispatch, useSelector } from "react-redux";
import { AppState } from "types";
import { allActions } from "redux/actions/allActions";

interface UserData {
    username: string,
    password: string
}

const INIT_STATE = {
  username: "",
  password: ""} as UserData;

export const LoginPage = () => {
  const [userData, setUserData] = useState({ ...INIT_STATE });
  const [errors, setErrors] = useState([] as string[]);

  const { loginInProgress } = useSelector(
    (state: AppState) => state.userReducer
  );

  const dispatch = useDispatch();

  const onChange = (name: string, value: string) => {
    setUserData({ ...userData, [name]: value });
  };

  const onLogin = () => {
    if (!validate()) return;
    dispatch(allActions.userActions.login(userData.username, userData.password));
  };

  const validate = () => {
    setErrors([]);
    const {
      username,
      password
    } = userData;

    const validateErrors = [];

    if (!username || username.length === 0) {
      validateErrors.push("Username is required");
    }
    if (!password || password.length === 0) {
        validateErrors.push("Password is required");
      }
    setErrors(validateErrors);
    return validateErrors.length === 0;
  };

  return (
    <React.Fragment>
      {loginInProgress && <Loader />}
      <div className={s.container}>
        <Panel>
                <TextField
                    required
                  title="Username"
                  value={userData.username}
                  name="username"
                  onChange={onChange}
                />
                <TextField
                required
                  title="Password"
                  value={userData.password}
                  name="password"
                  onChange={onChange}
                />
        </Panel>
      </div>
      {errors && errors.length > 0 && (
        <div>
          {errors.map((error) => (
            <p>{error}</p>
          ))}
        </div>
      )}
      <div className={s.buttons_container}>
        <Button title="Login" type="green" onClick={onLogin} />
      </div>
    </React.Fragment>
  );
};
